import { SingletonWebSocket } from './singletonWebSocket.js';
import { IntegrationAPI } from "./IntegrationApi.js";
import { loginService } from './Services/loginService.js';
import { callService } from './Services/callService.js';
import { callBarService } from './Services/callBarService.js';
import { notAvailableService } from './Services/notAvailableService.js';
import { assistedTransferService } from './Services/assistedTransferService.js';
import { chatService } from './Services/chatService.js';
import { callLogService } from './Services/callLogService.js';
import { dispositionCallService } from './Services/dispositionCallService.js';
import { agentDataService } from './Services/agentDataService.js';
import { reprogamCallService } from './Services/reprogamCallService.js';
import { StateRule } from './BusinessRule/StateRule';
import { ongoingCallIvrService } from './Services/ongoingCallIvrService.js';

export class IntegrationApiFactory {
	buildClient() {
		var params = {};
		params.serverIntegrationToken = null;
		SingletonWebSocket.currentStatus = "logout";
		var paramsService = { SingletonWebSocket, StateRule }
		params.SingletonWebSocket = SingletonWebSocket;
		params.loginService = new loginService(paramsService);
		params.callService = new callService(paramsService);
		params.notAvailableService = new notAvailableService(paramsService);
		params.callBarService = new callBarService(paramsService);
		params.assistedTransferService = new assistedTransferService(paramsService);
		params.chatService = new chatService(paramsService);
		params.callLog = new callLogService(paramsService);
		params.dispositionCallService = new dispositionCallService(paramsService);
		params.agentDataService = new agentDataService(paramsService);
		params.reprogamCallService = new reprogamCallService(paramsService);
		params.ongoingCallIvrService = new ongoingCallIvrService(paramsService);		
		return new IntegrationAPI(params);
	}
}


