export class callService {
    constructor(params) {
        this.SingletonWebSocket = params.SingletonWebSocket;
        this.StateRule = params.StateRule;
    }
    GetCampaignsRelated() {
        const action = "GetCampaignsRelated";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "OpenWindow",
                window: "ManualCall",
            }

            this.SingletonWebSocket.send(message);
        }
    }

    makeManualCall(phoneNum, campID, clientName, callKey) {
        const action = "makeManualCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "ManualCall",
                camId: campID,
                callKey: callKey,
                clientName: clientName,
                phoneNumber: phoneNum,
            }

            this.SingletonWebSocket.send(message);
        }
    }

    HangUpCall() {
        const action = "HangUp";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "CallAction",
                callAction: "HangUp"
            }

            this.SingletonWebSocket.send(message);
        }
    }
    HangUpAndLeaveMessage() {
        const action = "HangUpAndLeaveMessage";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "CallAction",
                callAction: "HangUpAndLeaveMessage"
            }
            this.SingletonWebSocket.send(message);
        }
    }
    HangUpManualDial() {
        const action = "HangUpManualDial";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = { client: "integration", action }
            this.SingletonWebSocket.send(message);
        }
    }

    UpdateDataCall(callOutID, data1, data2, data3, data4, data5) {
        const action = "UpdateDataCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                callOutID,
                client: "integration",
                data: [data1, data2, data3, data4, data5],
                action: "CallAction",
                callAction: "UpdateDataCall"
            }

            this.SingletonWebSocket.send(message);
        }
    }

    getTransfersOptions() {
        const action = "getTransfersOptions";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "GetTransferData"
            }

            this.SingletonWebSocket.send(message);
        }
    }
    transferCallToACD(transferCallToACDID) {
        const action = "transferCallToACD";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const data = {
                acdID: parseInt(transferCallToACDID),
                array: "",
                callKey: this.SingletonWebSocket.dataCall ? this.SingletonWebSocket.dataCall.CalKey : "",
                camId: this.SingletonWebSocket.dataCall.Id,
                dnis: this.SingletonWebSocket.dataCall.DNIS,
                extId: 0,
                name: "",
                reference: "dial",
                sPhone: this.SingletonWebSocket.dataCall.Phone,
                typeTransfer: 2
            };
            const message = {
                client: "integration",
                data: data,
                action
            };
            this.SingletonWebSocket.send(message);
        }
    }
    transferCallToAgent(transferCallToAgentID) {
        const action = "transferCallToAgent";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const data = {
                acdID: 1,
                datos: "",
                callKey: this.SingletonWebSocket.dataCall ? this.SingletonWebSocket.dataCall.CalKey : "",
                camId: this.SingletonWebSocket.dataCall.Id,
                dnis: this.SingletonWebSocket.dataCall.DNIS,
                extId: parseInt(transferCallToAgentID),
                name: "",
                reference: "dial",
                sPhone: this.SingletonWebSocket.dataCall.Phone,
                typeTransfer: 1
            }
            const message = { client: "integration", data, action }
            this.SingletonWebSocket.send(message);
        }
    }
    transferCallToPhoneNumber(transferCallToPhone) {
        const action = "transferCallToPhoneNumber";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const data = {
                acdID: 1,
                datos: "",
                callKey: this.SingletonWebSocket.dataCall ? this.SingletonWebSocket.dataCall.CalKey : "",
                camId: this.SingletonWebSocket.dataCall.Id,
                dnis: this.SingletonWebSocket.dataCall.DNIS,
                extId: 0,
                name: "",
                reference: "dial",
                sPhone: transferCallToPhone,
                typeTransfer: 0
            }
            const message = { client: "integration", data: data, action: "transferCallToPhoneNumber" }
            this.SingletonWebSocket.send(message);
        }
    }


}


