export class assistedTransferService {
    constructor(params) {
        this.SingletonWebSocket = params.SingletonWebSocket;
        this.StateRule = params.StateRule;
    }
    assistedDialNumber(phone) {
        const action = "assistedDialNumber";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            if (!(/^\d+$/.test(phone)) && (typeof onError === 'function')) {
                const error = { code: 8, message: "The phone should only contain numbers" }
                onError(error);
                return;
            }
            const message = {
                action,
                client: "integration",
                phone: phone
            }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerUseMainCall() {
        const action = "UseMainCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = { client: "integration", action }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerUseSecondCall() {
        const action = "UseSecondCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = { client: "integration", action }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerHangUP() {
        const action = "AssistedTransferCallHangUp";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "AssistedTransferCallHangUp",
            }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerTransferCalls() {
        const action = "TransferAssistedCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "TransferAssistedCall",
            }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerDropFirstCall() {
        const action = "DropFirstCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "DropFirstCall",
            }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerDropSecondCall() {
        const action = "DropSecondCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "DropSecondCall",
            }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerMakeConference() {
        const action = "MakeConference";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "MakeConference",
            }
            this.SingletonWebSocket.send(message);
        }
    }
    assistedXFerLeaveConference() {
        const action = "LeaveConference";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "LeaveConference",
            }
            this.SingletonWebSocket.send(message);
        }
    }
}


