export class loginService {
    constructor(params) {
        this.SingletonWebSocket = params.SingletonWebSocket;
        this.StateRule = params.StateRule;
    }
    login(username, password) {
        var action = "Login";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                user: username,
                password: password,
                action
            };
            this.SingletonWebSocket.send(message);
        }
    }

    CloseSession() {
        var action = "Logout";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action
            }

            this.SingletonWebSocket.send(message);
            this.SingletonWebSocket.currentState = "logout";
        }
    }

    SetReady() {
        var action = "setReady";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "setReady"
            }

            this.SingletonWebSocket.send(message);
        }
    }
    ChangePassword(aCurrentPassword, aNewPassword) {
        var action = "ChangePassword";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                aCurrentPassword: aCurrentPassword,
                aNewPassword: aNewPassword,
                action: "Settings",
                subaction: action
            };
            this.SingletonWebSocket.send(message);
        }
    }
}   


