export const STRATEGY_ASSISTED_TRANSFER = {
  "onSecondCallConected": function () {
    onSecondCallConected();
  },
  "onSecondCallHangUp": function () {
    onSecondCallHangUp();
  },
  "onSecondCall": function (json) {
    onSecondCall(json.data);
  },
  "onMainCall": function (json) {
    if (typeof onMainCall === 'function') {
      onMainCall(json.data);
    }
  },
  "onConference": function (json) {
    if (typeof onConference === 'function') {
      onConference();
    }
  },
  "onNextCall": function (json) {
    if (typeof onNextCall === 'function') {
      onNextCall(json.data);
    }
  },
}


