import { SingletonWebSocket } from './singletonWebSocket.js';
import { STRATEGY_CHAT } from '../src/Strategies/strategyChat';
import { STRATEGY_ASSISTED_TRANSFER } from '../src/Strategies/strategyAssistedTransfer';
import { STRATEGY_DISPOSITION } from '../src/Strategies/strategyDispositon';
import { STRATEGY_MANUAL_CALL } from '../src/Strategies/strategyManualCall';
import { STRATEGY_CALL_FLOW } from '../src/Strategies/strategyCallFlow';
import { STRATEGY_ONGOING_CALL_IVR } from '../src/Strategies/strategyOngoingCallIvr';
import { SingletonSharedData } from '../src/Singleton/SingletonSharedData';

export const STRATEGY_MESSAGES = {
    "getUnavailables": function (json) {
        if (typeof onUnavailableTypes === 'function') {
            onUnavailableTypes(json.array);
        }
    },
    "agentStatusChange": function (json) {
        SingletonWebSocket.currentStatus = json.data.currentState;
        if (typeof onAgentStatus === 'function') {
            onAgentStatus(json.data);
        }
    },
    "dataCall": function (json) {
        SingletonWebSocket.dataCall = json.data;
    },
    "extraDataCall": function (json) {
        SingletonWebSocket.dataCall = json.data;
    },
    "Chat": function (json) {
        if (STRATEGY_CHAT[json.subaction]) {
            STRATEGY_CHAT[json.subaction](json);
        }
    },
    "AssistedTransfer": function (json) {
        if (STRATEGY_ASSISTED_TRANSFER[json.subaction]) {
            STRATEGY_ASSISTED_TRANSFER[json.subaction](json);
        }
    },
    "TransferOption": function (json) {
        if (typeof onTransferOptions === 'function') {
            onTransferOptions(json);
        }
    },
    "onUnavailableHistory": (json) => {
        if (typeof onUnavailableHistory === 'function') {
            onUnavailableHistory(json.data);
        }
    },
    "onCampaigns": (json) => {
        if (typeof onCampaigns === 'function') {
            var data = [];
            for (var index in json.data) {
                data.push({ id: json.data[index].ID, name: json.data[index].Description });
            };
            onCampaigns(data);
        }
    },
    "onCallHistory": function (json) {
        if (typeof onCallHistory === 'function') {
            let data = json.data;
            for (var i in data) {
                delete data[i].imageItem
                delete data[i].imageBackItem
            }
            json.data = data;
            onCallHistory(json);
        }
    },
    "Disposition": function (json) {
        if (STRATEGY_DISPOSITION[json.subaction]) {
            STRATEGY_DISPOSITION[json.subaction](json);
        }
    },
    "CallBar": function (json) {
        if (STRATEGY_CALL_BAR[json.subaction]) {
            STRATEGY_CALL_BAR[json.subaction](json);
        }
    },
    "ManualCall": function (json) {
        if (STRATEGY_MANUAL_CALL[json.subaction]) {
            STRATEGY_MANUAL_CALL[json.subaction](json);
        }
    },
    "onLogin": () => {
        if (typeof onLogin === 'function') {
            onLogin();
        }
    },
    "onLogOut": () => {
        SingletonWebSocket.currentStatus = "logout";
        SingletonSharedData.clear();
        let logoutState = { currentState: SingletonWebSocket.currentStatus };
        if (typeof onLogOut === 'function') {
            onLogOut();
            onAgentStatus(logoutState);
        }
    },
    "CallFlow": function (json) {
        if (STRATEGY_CALL_FLOW[json.subaction]) {
            STRATEGY_CALL_FLOW[json.subaction](json);
        }
    },
    "GetAgentID": function (json) {
        if (typeof agentId === 'function') {
            agentId(json);
        }
    },
    "GetUserName": function (json) {
        if (typeof userName === 'function') {
            userName(json);
        }
    },
    "GetExtension": function (json) {
        if (typeof extension === 'function') {
            extension(json);
        }
    },
    "onCallEnds": (message) => {
        if (typeof onCallEnds === 'function') {
            onCallEnds(message.data);
        }
    },
    "onDialingNumber": (message) => {
        if (typeof onDialingNumber === 'function') {
            onDialingNumber(message.data);
        }
    },
    "onPasswordUpdated": () => {
        if (typeof onPasswordUpdated === 'function') {
            onPasswordUpdated();
        }
    },
    "errorOnPasswordChange": (message) => {
        if (typeof errorOnPasswordChange === 'function') {
            errorOnPasswordChange(message.data);
        }
    },
    "onErrorPasswordUpdate": (message) => {
        if (typeof onErrorPasswordUpdate === 'function') {
            onErrorPasswordUpdate(message.data);
        }
    },
    "onError": (message) => {
        if (typeof onError === 'function') {
            onError(message.data);
        }
    },
    "remoteLoginError": (message) => {
        SingletonSharedData.username = "";
        if (typeof remoteLoginError === 'function') {
            remoteLoginError(message.data);
        }
    },
    "onCallsOnQueue": (message) => {
        if (typeof onCallsOnQueue === 'function') {
            onCallsOnQueue(message.data);
        }
    },
    "errorOnUnavailableStatus": (message) => {
        if (typeof errorOnUnavailableStatus === 'function') {
            errorOnUnavailableStatus(message.data);
        }
    },
    "errorOnUnavailableStatusHistory": (message) => {
        if (typeof errorOnUnavailableStatusHistory === 'function') {
            errorOnUnavailableStatusHistory(message.data);
        }
    },
    "errorOnCampaignsRelated": (message) => {
        if (typeof errorOnCampaignsRelated === 'function') {
            errorOnCampaignsRelated(message.data);
        }
    },
    "errorOnDialProcess": (message) => {
        if (typeof errorOnDialProcess === 'function') {
            errorOnDialProcess(message.data);
        }
    },
    "errorOnDispositionList": (message) => {
        if (typeof errorOnDispositionList === 'function') {
            errorOnDispositionList(message.data);
        }
    },
    "errorOnDispositionSelected": (message) => {
        if (typeof errorOnDispositionSelected === 'function') {
            errorOnDispositionSelected(message.data);
        }
    },
    "errorOnDispositionsPhonesList": (message) => {
        if (typeof errorOnDispositionsPhonesList === 'function') {
            errorOnDispositionsPhonesList(message.data);
        }
    },
    "errorOnPhoneNumbersList": (message) => {
        if (typeof errorOnPhoneNumbersList === 'function') {
            errorOnPhoneNumbersList(message.data);
        }
    },
    "errorOnCallHistory": (message) => {
        if (typeof errorOnCallHistory === 'function') {
            errorOnCallHistory(message.data);
        }
    },
    "OngoingCallIvr": function (json) {
        if (STRATEGY_ONGOING_CALL_IVR[json.subaction]) {
            STRATEGY_ONGOING_CALL_IVR[json.subaction](json);
        }
    },
    //"onReprogramCall"(DispositionResult) {
    //     console.log("onReprogramCall");
    //    createTrigger.trigger("onReprogramCall", DispositionResult);
    //}
}




