export class dispositionCallService {
    constructor(params) {
        this.SingletonWebSocket = params.SingletonWebSocket;
        this.StateRule = params.StateRule;
    }
    getCampaignDispositions(campaignId) {
        var action = "getCampaignDispositions";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "OpenWindow",
                window: "Disposition"
            }
            this.SingletonWebSocket.send(message);

            const message2 = {
                client: "integration",
                campaignId: campaignId,
                action: "getCampaignDispositions"
            };
            setTimeout(() => { this.SingletonWebSocket.send(message2); }, 500);
        }
    }

    disposeCampaingCall(dispositionId, camId, callID, subId) {
        var action = "disposeCampaingCall";
        let time = 0;
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {

            const messageDispose = {
                client: "integration",
                DispositionId: dispositionId,
                CamId: camId,
                CallID: callID,
                SubId: subId,
                action: "Disposition",
                subaction: "disposeCampaingCall"
            };

            if(this.SingletonWebSocket.currentStatus === "Dialog" && dispositionId && camId && callID ){
                time = 500;
                const messageOpen = {
                    client: "integration",
                    action: "OpenWindow",
                    window: "Disposition"
                };
                this.SingletonWebSocket.send(messageOpen);
            }
            setTimeout(() => { this.SingletonWebSocket.send(messageDispose); }, time);
        }
    }

    getACDDispositions(acdId) {
        var action = "getACDDispositions";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                action: "OpenWindow",
                window: "Disposition"
            }
            this.SingletonWebSocket.send(message);

            const message2 = {
                client: "integration",
                acdId: acdId,
                action: "getACDDispositions"
            };
            setTimeout(() => { this.SingletonWebSocket.send(message2); }, 500);
        }
    }

    disposeACDCall(dispositionId, callID, subId) {
        var action = "disposeACDCall";
        if (this.StateRule.validate(this.SingletonWebSocket.currentStatus, action)) {
            const message = {
                client: "integration",
                DispositionId: dispositionId,
                CallID: callID,
                SubId: subId,
                action: "Disposition",
                subaction: "disposeACDCall"
            };
            this.SingletonWebSocket.send(message);
        }
    }
}



