let _agentUsername = "";
export class SingletonSharedData {

    static set username(username) {
        _agentUsername = username;
        console.log("SetUsername ", _agentUsername);
    }
    static get username() { return _agentUsername; }

    static clear() {
        _agentUsername = "";
    }
}


